
import React from 'react'
import { Badge, Container,  FormControl, Nav, Navbar,Dropdown } from 'react-bootstrap'
import { Link } from 'react-router-dom';

const Header = () => {
    return (
        <>
            <Navbar bg="dark" variant="dark" style={{ height: 70 }}>
                <Container>
                    <Navbar.Brand><Link to="/">Shoping Cart</Link></Navbar.Brand>
                    <Navbar.Text className="search">
                        <FormControl style={{ width: "500px" }}
                            type="search"
                            placeholder="Search a product"
                            className="ml-2"
                            aria-label="Search"
                        />
                    </Navbar.Text>
                    <Nav>
                        <Dropdown drop="start">
                            <Dropdown.Toggle variant="success">
                            <i className="fas fa-cart-plus" style={{color:"white", fontSize:"25px"}}></i>
                            <Badge>{10}</Badge>
                            </Dropdown.Toggle>
                            <Dropdown.Menu style={{minWidth:"370px" , marginTop:"45px"}}>
                                <div style={{margin:"0 15px"}}>Cart is Empty !</div>
                            </Dropdown.Menu>
                            
                        </Dropdown>
                    </Nav>

                </Container>

            </Navbar>
        </>
    )
}

export default Header;
